
h1,h2,h3,h4,h5,h6{
    margin: 1rem 0 1rem 0;
}
h6{
    font-weight: normal;
}
.goods-price_freight{
    margin: 1rem auto 1rem 1rem;
}
.goods-price{
    font-size: 3rem;
}
.goods-freight{
    font-size: 2rem;
}
.sales-status-show{
    text-align: left;
    font-size: 1rem;
    padding: 0;
}
.sales-status-show li{
    display: inline-block;
    width: 8rem;
    border: 1px solid red;
    background-color: aqua;
}
.goods-spec-choice-div{
    font-size: 2rem;
    line-height: 2.5rem;
    margin: 0.5rem auto 0.5rem auto;
}
.goods-spec-choice-div:after{
    content: "";
    display: block;
    clear: both;
}
.goods-spec-choice{
    float: left;
    margin-right: 1rem;
}
.goods-spec-choice-div ul{
    line-height: 2.5rem;
    vertical-align: middle;
}
.goods-spec-choice-div li,.goods-spec-choice-div input{
    font-size: 1.5rem;
    background-color: aqua;
    float: left;
    margin-right: 1rem;
    border: 1px solid red;
}
.goods-spec-choice-div input{
    width: 5rem;
}
#number-input{
    display: inline-block;
}
/*全部用label代替商品规格选择div*/
.goods-spec-choice-div input{
    display: none;
}
.goods-spec-choice-div label{
    font-size: 1.5rem;
    background-color: aqua;
    margin-right: 0.5rem;
    border: 1px solid red;
}
.selected{
    color: red;
}
.cursor-hand{
    cursor: pointer;
}
/*购买按键*/
.buy-btn-div{
    font-size: 2rem;
}
.buy-btn,.shop-cart-btn{
    display: inline-block;
    height: 3rem;
    width: 12rem;
    background-color: red;
    border-radius: 0.5rem;
    text-align: center;
    line-height: 3rem;
    box-shadow: 3px 3px rgba(255,0,0,0.5);
}
.buy-btn:hover,.shop-cart-btn:hover{
    background-color: rgba(0,255,0,1);
    box-shadow: 3px 3px rgba(0,255,0,0.5);
    cursor: pointer;
}
.buy-btn:active,.shop-cart-btn:active{
    background-color: inherit;
    cursor: pointer;
}



