
.goods-total{
    text-align: center;
}
/*表格样式*/
/*只让chrome显示样式*/
@media all and (min-width: 0){
.cart-table{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        width: 70rem;
}
.cart-tr{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        text-align: center;
}
.cart-td{
        border: 1px red solid;
}
.is-check{
        -ms-flex: 1 0 4rem;
            flex: 1 0 4rem;
}
.goods-img{
        -ms-flex: 2 0 8rem;
            flex: 2 0 8rem;
}
.goods-name{
        -ms-flex: 3 0 12rem;
            flex: 3 0 12rem;
}
.price{
        -ms-flex: 2 0 6rem;
            flex: 2 0 6rem;
}
.number{
        -ms-flex: 1 0 4rem;
            flex: 1 0 4rem;
}
.total-class{
        -ms-flex: 2 0 8rem;
            flex: 2 0 8rem;
}
.handle{
        -ms-flex: 1 0 4rem;
            flex: 1 0 4rem;
}
}
/*只让火狐显示样式*/
@-moz-document url-prefix(){
.cart-table{
        display: table;
        margin-top: 1rem;
        margin-bottom: 1rem;
}
.cart-tr{
        display: table-row;
        height: 5rem;
        position: relative;
        margin-top: 1rem;
        margin-bottom: 1rem;
}
.cart-td{
        display: table-cell;
}
}



