
.login-all{
  display: inline;
}
.login{
  float: right;
  margin: 0 1rem 0 1rem;
}
.login-div{
  z-index: 990;
  position: fixed;
  left: 50%;
  top:50%;
  transform: translate(-50%,-50%);
  background-color: aqua;
  border: 1px solid red;
  width: 20rem;
  height: 13rem;
}
.title{
  position: relative;
}
.title-text{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  margin: 0;
  padding: 0;
}
.closeSymbol{
  float: right;
  z-index: 991;
  margin: 5px;
}
.login-tr{
  display: block;
  margin: 1rem auto 1rem auto;
  text-align: center;
}
.title-close{
  margin: 0;
  background-color: brown;
}
.login-tr:after{
  content: "";
  display: block;
  clear: both;
}
.login-tr span{
  display: inline-block;
  width: 4rem;
}
.validate-refresh{
  width: 2.5rem;
  padding: 0;
  box-sizing: border-box;
}
#validate-number{
  width: 2.5rem;
}
.validate-show{
  vertical-align: middle;
  margin-left: 2px;
  margin-right: 2px;
}
.save-login-label{
  font-size: 0.5rem;
  vertical-align: middle;
  display: inline-block;
  margin-left: 1rem;
  line-height: 1rem;
}
#save-login{
  vertical-align: middle;
}



