
.pic{
  position: absolute;
  left: 0;
}
.big-img{
  cursor: pointer;
}
.small-img-ul{
  text-align: center;
  margin: 0;
  padding: 0;
}
.small-img-ul:after{
  content: "";
  display: block;
  clear: both;
}
.small-img-ul li{
  display: inline-block;
  margin-left: 1rem;
  box-sizing: border-box;
  border: 1px solid white;
}
.small-img{
  cursor: pointer;
}
li.redBorder{
  border: 1px solid red;
}



